import type { Datum, Value } from "../types";

type Calculator = (parameters: {
  currentValue: Value;
  updatedValues: Datum;
  originalValues: Datum;
}) => Value;

const convertNumber = (values: Datum, field: string) => {
  return values[field] ? Number(values[field]) : 0;
};

export const calculators = new Map<string, [string[], Calculator]>([
  [
    "out:Initial Cost/MSEK",
    [
      [
        "out:Windows(kr/m2)",
        "out:Window surface/m2",
        "out:IMD (#apts)",
        "out:IMD (kr/apt)",
        "out:PV (kr/kWp)",
        "out:PV (kWp installed)",
        "out:Fasadisolering (kr/m2)",
        "out:Facade surface/m2",
        "out:kr/heat exchanger",
        "out:number of heat exchangers",
        "out:cost/m2 injustering",
        "out:Atemp",
        "out:New AHU Cost/SEK",
        "out:EAHP (Heatpump)",
        "out:EAHP (1000ltanks)",
        "out:EAHP (pipework)",
        "out:EAHP (natural ventilation grills)",
        "out:cost_raised roof",
        "out:EAHP (installation)",
        "out:EAHP (replacement cost)",
        "out:FTX (AHU)",
        "out:FTX (Ductwork)",
        "out:FTX (Diffusers)",
        "out:FTX (Dampers)",
        "out:FTX (Roof)",
        "out:FTX (Facade)",
        "out:FTX (Replacement Cost)",
        "out:FTX (S to F improvement cost)",
        "out:EAHP (S to F improvement cost)",
      ],
      ({ currentValue, updatedValues, originalValues }) => {
        return (
          Number(currentValue) * 1_000_000 -
          convertNumber(originalValues, "out:Windows(kr/m2)") *
            convertNumber(originalValues, "out:Window surface/m2") +
          convertNumber(updatedValues, "out:Windows(kr/m2)") *
            convertNumber(updatedValues, "out:Window surface/m2") -
          convertNumber(originalValues, "out:IMD (kr/apt)") *
            convertNumber(originalValues, "out:IMD (#apts)") +
          convertNumber(updatedValues, "out:IMD (kr/apt)") *
            convertNumber(updatedValues, "out:IMD (#apts)") -
          convertNumber(originalValues, "out:PV (kr/kWp)") *
            convertNumber(originalValues, "out:PV (kWp installed)") +
          convertNumber(updatedValues, "out:PV (kr/kWp)") *
            convertNumber(updatedValues, "out:PV (kWp installed)") -
          convertNumber(originalValues, "out:Facade surface/m2") *
            convertNumber(originalValues, "out:Fasadisolering (kr/m2)") +
          convertNumber(updatedValues, "out:Facade surface/m2") *
            convertNumber(updatedValues, "out:Fasadisolering (kr/m2)") -
          convertNumber(originalValues, "out:kr/heat exchanger") *
            convertNumber(originalValues, "out:number of heat exchangers") +
          convertNumber(updatedValues, "out:kr/heat exchanger") *
            convertNumber(updatedValues, "out:number of heat exchangers") -
          convertNumber(originalValues, "out:Atemp") *
            convertNumber(originalValues, "out:cost/m2 injustering") +
          convertNumber(updatedValues, "out:Atemp") *
            convertNumber(updatedValues, "out:cost/m2 injustering") -
          convertNumber(originalValues, "out:New AHU Cost/SEK") +
          convertNumber(updatedValues, "out:New AHU Cost/SEK") -
          convertNumber(originalValues, "out:EAHP (Heatpump)") +
          convertNumber(updatedValues, "out:EAHP (Heatpump)") -
          convertNumber(originalValues, "out:EAHP (1000ltanks)") +
          convertNumber(updatedValues, "out:EAHP (1000ltanks)") -
          convertNumber(originalValues, "out:EAHP (pipework)") +
          convertNumber(updatedValues, "out:EAHP (pipework)") -
          convertNumber(
            originalValues,
            "out:EAHP (natural ventilation grills)",
          ) +
          convertNumber(
            updatedValues,
            "out:EAHP (natural ventilation grills)",
          ) -
          convertNumber(originalValues, "out:cost_raised roof") +
          convertNumber(updatedValues, "out:cost_raised roof") -
          convertNumber(originalValues, "out:EAHP (installation)") +
          convertNumber(updatedValues, "out:EAHP (installation)") -
          convertNumber(originalValues, "out:EAHP (replacement cost)") +
          convertNumber(updatedValues, "out:EAHP (replacement cost)") -
          convertNumber(originalValues, "out:FTX (AHU)") +
          convertNumber(updatedValues, "out:FTX (AHU)") -
          convertNumber(originalValues, "out:FTX (Ductwork)") +
          convertNumber(updatedValues, "out:FTX (Ductwork)") -
          convertNumber(originalValues, "out:FTX (Diffusers)") +
          convertNumber(updatedValues, "out:FTX (Diffusers)") -
          convertNumber(originalValues, "out:FTX (Dampers)") +
          convertNumber(updatedValues, "out:FTX (Dampers)") -
          convertNumber(originalValues, "out:FTX (Roof)") +
          convertNumber(updatedValues, "out:FTX (Roof)") -
          convertNumber(originalValues, "out:FTX (Facade)") +
          convertNumber(updatedValues, "out:FTX (Facade)") -
          convertNumber(originalValues, "out:FTX (Replacement Cost)") +
          convertNumber(updatedValues, "out:FTX (Replacement Cost)") -
          convertNumber(originalValues, "out:FTX (S to F improvement cost)") +
          convertNumber(updatedValues, "out:FTX (S to F improvement cost)") -
          convertNumber(originalValues, "out:EAHP (S to F improvement cost)") +
          convertNumber(updatedValues, "out:EAHP (S to F improvement cost)") -
          0
        );
      },
    ],
  ],

  [
    "out:Payback Time",
    [
      ["out:ROI %"],
      ({ updatedValues }) => {
        return 100 / Number(updatedValues["out:ROI %"]);
      },
    ],
  ],

  [
    "out:ROI %",
    [
      ["out:Total besparing (kr)", "out:Initial Cost/MSEK"],
      ({ updatedValues }) => {
        const totalInstallationCost = Number(
          updatedValues["out:Total besparing (kr)"],
        );
        const initialCost = Number(updatedValues["out:Initial Cost/MSEK"]);

        return (totalInstallationCost / initialCost) * 100;
      },
    ],
  ],
]);
